import React, { Fragment } from 'react'
import project_one from '../img/project_one.png'
import project_two from '../img/project_two.png'

function Projects() {
    return (
        <Fragment>
            <h1 className="text-3xl font-semibold text-black pb-4">Projects</h1>
            <div className="p-10 grid grid-cols-1 sm:grid-cols-1 md:grid-cols-3 lg:grid-cols-3 xl:grid-cols-3 gap-5">
                {/*Card*/}
                <div class="rounded overflow-hidden shadow-lg transform hover:-translate-y-1 hover:scale-104 duration-200">
                    <img class="w-full" src={project_one} alt="Mountain" />
                    <div class="px-6 py-4">
                        <div class="font-bold text-xl mb-2">My First ReactApp - Tasks</div>
                        <p class="text-gray-700 text-base">
                            Mi primera aplicacion con el Framework React
                            </p>
                    </div>
                    <div class="px-6 pt-4 pb-2">
                        <span class="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 mb-2">#beginer</span>
                        <span class="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 mb-2">#react</span>
                        <span class="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 mb-2">#tailwindCss</span>
                    </div>
                </div>
                {/*Card*/}
                <div class="rounded overflow-hidden shadow-lg transform hover:-translate-y-1 hover:scale-104 duration-200">
                    <img class="w-full" src={project_two} alt="Mountain" />
                    <div class="px-6 py-4">
                        <div class="font-bold text-xl mb-2">Inventory</div>
                        <p class="text-gray-700 text-base">
                            Aplicacion web usada para el registro de productos y administracion de ventas de la empresa SjVidrios
                            </p>
                    </div>
                    <div class="px-6 pt-4 pb-2">
                        <span class="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 mb-2">#react</span>
                        <span class="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 mb-2">#firebase</span>
                        <span class="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 mb-2">#bootstrap</span>
                    </div>
                </div>
            </div>

        </Fragment>
    )
}

export default Projects
