import React from 'react'
import HtmlLogo from '../img/HTML5_logo.png'
import CssLogo from '../img/css3_logo.png'
import JsLogo from '../img/js_logo.png'
import ResponsiveDesingLogo from '../img/rd_logo.png'
import FirebaseLogo from '../img/firebase_logo.png'
import ReactLogo from '../img/react_logo.png'


function TechSkills() {
    return (
        <div className=" py-6 px-6 text-center bg-black">
            <h1 className="text-3xl font-semibold text-white pb-6">Tecnologies</h1>
            <div className="flex flex-wrap justify-evenly text-center items-center">
                <div className="mb-4 hover:scale-50">
                    <img className="h-20 transform hover:-translate-y-2 hover:scale-110 duration-300"
                        src={HtmlLogo} alt="HTML5" />
                </div>
                <div className="mb-4">
                    <img className="h-20 transform hover:-translate-y-2 hover:scale-110 duration-300"
                        src={CssLogo} alt="" />
                </div>
                <div className="mb-4">
                    <img className="h-16 transform hover:-translate-y-2 hover:scale-110 duration-300 rounded-lg"
                        src={JsLogo} alt="" />

                </div>
                <div className="mb-4">
                    <img className="h-20 transform hover:-translate-y-2 hover:scale-110 duration-300 rounded-lg"
                        src={ResponsiveDesingLogo} alt="" />
                </div>
                <div className="mb-4">
                    <img className="h-16 transform hover:-translate-y-2 hover:scale-110 duration-300 rounded-lg"
                        src={FirebaseLogo} alt="" />
                </div>
                <div className="mb-4">
                    <img className="h-20 transform hover:-translate-y-2 hover:scale-110 duration-300 rounded-lg"
                        src={ReactLogo} alt="" />
                </div>
            </div>
        </div>
    )
}

export default TechSkills
